import * as d3 from "d3";
import * as utils from "./utils";
import v1_5Img from "../assets/v1_5.png";
import v1_5 from "../assets/v1_5.json";
import * as styles from "../css/app.module.css";

function renderChart() {
  const params = new URL(document.location).searchParams;
  const userData = utils.decodeUserData(params.get("data"));

  const data = v1_5.data;

  data.forEach((datum) => {
    datum.selected =
      userData && userData.data
        ? userData.data.find((d) => d.key === datum.key)
        : false;
  });

  const getKeyElement = (p) => (d) => d.key.split("-")[p];
  const getKeyCol = getKeyElement(0);
  const getKeyRow = getKeyElement(1);

  const chartWidth = 4296;
  const chartHeight = 2271;
  const selectionMargin = { left: 115, top: 325 };
  const selectionWidth = 173;
  const selectionHeight = 320;

  const element = document.getElementById("hbr-chart-container");
  const svg = d3
    .select(element)
    .append("svg")
    .attr("viewBox", `0 0 ${chartWidth} ${chartHeight}`);

  svg
    .append("image")
    .attr("href", v1_5Img)
    .attr("width", chartWidth)
    .attr("height", chartHeight);

  const selectionGroup = svg
    .append("g")
    .attr(
      "transform",
      `translate(${selectionMargin.left}, ${selectionMargin.top})`
    );

  const renderSelection = () => {
    const updateSelection = (e, d) => {
      data.forEach((datum) => {
        if (datum.key === d.key) {
          datum.selected = !datum.selected;
        }
      });

      renderSelection();
    };

    selectionGroup
      .selectAll("rect")
      .data(data)
      .join("rect")
      .attr("x", (d) => getKeyCol(d) * selectionWidth)
      .attr("y", (d) => getKeyRow(d) * selectionHeight)
      .attr("width", selectionWidth)
      .attr("height", selectionHeight)
      .attr("rx", 8)
      .attr("ry", 8)
      .attr("fill-opacity", 0)
      .attr("stroke", (d) => (d.selected ? "red" : null))
      .attr("stroke-width", 12)
      .on("click", updateSelection);
  };

  const onCopyLink = () => {
    utils.copyPermaLink(v1_5, data);
    alert("已複製！");
  };

  const onExportData = () => {
    utils.exportData(v1_5, data);
  };

  const onExportImage = () => {
    utils.exportImage(chartWidth, chartHeight, svg.node());
  };

  document.getElementById("button-copy-link").onclick = onCopyLink;
  document.getElementById("button-export-data").onclick = onExportData;
  document.getElementById("button-export-image").onclick = onExportImage;

  renderSelection();
}

function main() {
  renderChart();
}

main();
