import { Canvg } from "canvg";

export const decodeUserData = (encoded) => {
  if (typeof encoded !== "string") {
    return [];
  }
  return JSON.parse(atob(encoded));
};

const toUserData = (originalData, userData) => ({
  ...originalData,
  data: userData.filter((d) => d.selected).map((d) => ({ key: d.key })),
});

export const encodeUserData = (originalData, userData) => {
  return btoa(JSON.stringify(toUserData(originalData, userData)));
};

export const copyPermaLink = (originalData, data) => {
  const url = new URL(document.location);
  url.searchParams.set("data", encodeUserData(originalData, data));
  navigator.clipboard.writeText(url.toString());
};

export const exportData = (originalData, userData) => {
  const userDataForDownload = JSON.stringify(
    toUserData(originalData, userData)
  );
  const url = window.URL.createObjectURL(new Blob([userDataForDownload]));
  downloadFile({
    file: url,
    name: "gyhbr",
    format: "json",
  });
};

export const exportImage = async (chartWidth, chartHeight, svg) => {
  const svgData = new XMLSerializer().serializeToString(svg);
  const canvas = document.createElement("canvas");
  const ctx = canvas.getContext("2d");

  canvas.width = chartWidth;
  canvas.height = chartHeight;

  const v = Canvg.fromString(ctx, svgData);

  await v.render();
  const dataUrl = canvas.toDataURL(`image/png`);
  downloadFile({
    file: dataUrl,
    name: "gyhbr",
    format: "png",
  });
};

const downloadFile = ({ file, name, format }) => {
  let a = document.createElement("a");
  a.download = `${name}.${format}`;
  a.href = file;
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
};
